@import "variables";
@import "footer";
@import "header";
@import "button";
@import "custom-datepicker";
@import "sign";
@import "input";
@import "star-rating";
@import "room-card";
@import "slider";


@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.ttf")format('ttf');
  src: url("../fonts/Montserrat-Regular.ttf")format('ttf');
}
@font-face {
  font-family: 'Quicksand';
  src: url("../fonts/Quicksand-Bold.ttf")format('ttf');
  src: url("../fonts/Quicksand-Regular.ttf")format('ttf');
  src: url("../fonts/Quicksand-Light.ttf")format('ttf');
  src: url("../fonts/Quicksand-Medium.ttf")format('ttf');
}
.h1{
  font-size: 24px;
  line-height: 29px;
  color: $darkShade100;
}
.h2{
  font-size: 19px;
  line-height: 23px;
}
body {
  font-family: "Montserrat","Quicksand", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: $darkShade75;


  .form-label, ._title{

    margin-bottom: 5px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    color: $darkShade100;
  }
  a{
    text-decoration: none;
  }
  .form_rooms{
    max-width: 380px;
  }


}
.dates{
  display: flex;
  #arrive{
    margin-right: 20px;
  }
}

.container{
  padding: 0 140px 0 140px;
  width: 100%;
  height: 100%;
}
main{
  .form{
    padding: 40px 30px 30px 30px;
    background: white;
    border: 1px solid #0000001F;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
  }
  .main-index{
    .index-block{
      margin-bottom: 386px;
    }
    background: url("../img/jpg/image 3.jpg") no-repeat;
    background-size: cover;
    padding: 70px 0 18px 0;

    .main-wrapper{
      width: 100%;
      height: 100%;
      justify-content: space-between;

      .descr{
        font-size: 14px;
        color: $darkShade75;
        text-align: right;
        align-self: flex-end;
      }
    }
  }

  .main-sign{
    background: url("../img/jpg/image 4.jpg") center no-repeat;
    background-size:cover;
    .form-box{
      padding-top:20px ;
      padding-bottom: 20px;
      display: flex;
      justify-content: center;
      .to-create{
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
      }
      .form_sign{
        margin: 172px 0 172px 0;

      }
      .form_reg{
        .genders{
          display: flex;

        }
      }
    }
  }
  .main-rooms{

    .container{
      display: flex;

    }
    .left{
      margin-right: 60px;
      .left-block{

      }
      .dropdown{
        .dropdown-content{
          margin-top: 18px;
          display: none;
        }
        #toggle{
          display: none;
        }
        .toggle{
          position: absolute;
          font-size: 24px;
          color: $darkShade50;
          cursor: pointer;
        }

        label._title{
          width: 100%;
          &:after {
            margin-left: 23px;
            position: absolute;
            font-family: 'Material Icons';
            content: "expand_more";
            text-transform: none;
            font-size: 24px;
            color: $darkShade50;
            cursor: pointer;
          }
        }
        input[type=checkbox]:checked ~ .dropdown-content{
          display: block;
        }
        input[type=checkbox]:checked ~ ._title:after{
          position: absolute;
          -webkit-transform: rotate(180deg);
          -moz-transform: rotate(180deg);
          transform: rotate(180deg);
        }

      }
    }
    .rooms{
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-column-gap:10px;
      grid-row-gap: 10px;
    }
  }
}
